import React, { useState, useEffect } from "react";

const Page1 = () => {
  const [phase, setPhase] = useState("years"); // : "years"  "text"
  const [year, setYear] = useState(12); //  
  const [index, setIndex] = useState(0); //   
  const [showScrollIcon, setShowScrollIcon] = useState(false); //  scroll-icon

  //   
  const texts = [
    "We are JET24",
    "Just Enjoy Time",
    "The ambitious team and <br /> visionary individuals with <br /> big passion for aviation and sky.",
  ];

  //   
  useEffect(() => {
    if (phase === "years") {
      const yearInterval = setInterval(() => {
        setYear((prev) => {
          if (prev < 24) return prev + 1;
          clearInterval(yearInterval); //   
          setTimeout(() => setPhase("text"), 1000); //     1 
          return prev;
        });
      }, 300); //    300 
      return () => clearInterval(yearInterval);
    }
  }, [phase]);

  //   
  useEffect(() => {
    if (phase === "text") {
      const textInterval = setInterval(() => {
        setIndex((prev) => {
          if (prev < texts.length - 1) return prev + 1;
          clearInterval(textInterval); //    
          setTimeout(() => setShowScrollIcon(true), 500); //  scroll-icon  1 
          return prev;
        });
      }, 1500); //    1.5 
      return () => clearInterval(textInterval);
    }
  }, [phase, texts.length]);

  return (
    <div style={styles.page} id="main">
      <div className="we__container">
        {/*   */}
        {phase === "years" && (
          <div className="we__year-container">
            <span className="we__start-text">2012  20</span>
            <div className="we__scroll-wrapper">
              <div
                className="we__scroll-content"
                style={{
                  transform: `translateY(-${(year - 12) * 4}rem)`, //   rem,  font-size
                }}
              >
                {Array.from({ length: 13 }, (_, i) => (
                  <div key={i} className="we__scroll-item">
                    {i + 12}
                  </div>
                ))}
              </div>
            </div>
          </div>
        )}

        {/*   */}
        {phase === "text" && (
          <div className="we__text-container">
            {texts.map((text, i) => (
              <p
                key={i}
                className={`we__text ${index === i ? "visible" : ""}`}
                style={{ display: index === i ? "block" : "none" }}
                dangerouslySetInnerHTML={{ __html: text }} //    HTML
              ></p>
            ))}
          </div>
        )}

        {/*   scroll-icon */}
        <div
          className={`scroll-icon-container ${showScrollIcon ? "visible" : ""}`}
        >
          <svg
            className="scroll-icon"
            width="26"
            height="42"
            viewBox="0 0 26 42"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            {/*   */}
            <rect
              x="1"
              y="1"
              width="24"
              height="40"
              rx="12"
              stroke="#616161"
              strokeWidth="1.5"
            ></rect>
            {/*   */}
            <rect
              className="dot"
              x="11"
              y="6"
              width="4"
              height="6.66667"
              rx="2"
              fill="#9E9E9E"
            ></rect>
          </svg>
        </div>
      </div>
    </div>
  );
};

const styles = {
  page: {
    minWidth: "100vw",
    height: "100vh",
    position: "relative",
  },
};

export default Page1;
